<template>
    <div>
        <h1>Login</h1>
        <!-- 로그인 폼: 로그인 상태가 아닐 때만 표시 -->
        <form v-if="!isLoggedIn" @submit.prevent="login">
            <!-- 사용자 이름 입력 필드 -->
            <input v-model="username" placeholder="Username" required :disabled="loading" />
            <!-- 비밀번호 입력 필드 -->
            <input v-model="password" type="password" placeholder="Password" required :disabled="loading" />
            <!-- 로그인 버튼 -->
            <button type="submit" :disabled="loading">
                {{ loading ? 'Logging in...' : 'Login' }}
            </button>
        </form>

        <!-- 로그아웃 버튼: 로그인 상태일 때만 표시 -->
        <button v-if="isLoggedIn" @click="logout">Logout</button>

        <!-- 에러 메시지 표시 -->
        <p v-if="error" class="error">{{ error }}</p>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            username: '', // 사용자 이름 입력값
            password: '', // 비밀번호 입력값
            error: '', // 에러 메시지
            loading: false, // 로딩 상태
            isLoggedIn: false // 로그인 상태
        };
    },
    created() {
        // 컴포넌트가 로드될 때 localStorage에서 로그인 상태를 확인
        this.isLoggedIn = localStorage.getItem('isLoggedIn') === 'true';
    },
    methods: {
        async login() {
            this.error = ''; // 이전 에러 메시지 초기화
            this.loading = true; // 로딩 시작

            try {
                // 로그인 요청
                const response = await axios.post('/api/auth/login.php', {
                    username: this.username,
                    password: this.password
                });

                if (response.data.success) {
                    this.isLoggedIn = true; // 로그인 상태 설정
                    localStorage.setItem('isLoggedIn', 'true'); // 로그인 상태를 localStorage에 저장
                    // 로그인 성공 시 메인 페이지로 이동
                    this.$router.push('/');
                    this.username = ''; // 입력 필드 초기화
                    this.password = '';
                } else {
                    // 서버에서 반환된 에러 메시지 표시
                    this.error = response.data.message || 'Login failed';
                }
            } catch (err) {
                console.error(err); // 디버깅을 위한 콘솔 출력
                this.error = err.response?.data?.message || 'An unexpected error occurred';
            } finally {
                this.loading = false; // 로딩 종료
            }
        },
        logout() {
            this.isLoggedIn = false; // 로그아웃 상태 설정
            localStorage.removeItem('isLoggedIn'); // localStorage에서 로그인 상태 제거
            this.$router.push('/'); // 로그아웃 후 메인 페이지로 이동
        }
    }
};
</script>

<style scoped>
/* 에러 메시지 스타일 */
.error {
    color: red;
    margin-top: 10px;
}

/* 비활성화된 버튼 스타일 */
button:disabled {
    background-color: #ccc; /* 비활성화 시 배경색 */
    cursor: not-allowed; /* 비활성화 시 커서 변경 */
}
</style>
