<template>
    <div>
        <h1>Register</h1>
        <form @submit.prevent="register">
            <!-- 사용자 이름 입력 필드 -->
            <input v-model="username" placeholder="Username" required />
            <!-- 비밀번호 입력 필드 -->
            <input v-model="password" type="password" placeholder="Password" required />
            <!-- 제출 버튼 -->
            <button type="submit">Register</button>
        </form>
        <!-- 에러 메시지 표시 -->
        <p v-if="error" class="error">{{ error }}</p>
        <!-- 성공 메시지 표시 -->
        <p v-if="success" class="success">{{ success }}</p>
    </div>
</template>

<script>
import axios from 'axios'; // HTTP 요청을 처리하기 위한 Axios 라이브러리

export default {
    data() {
        return {
            username: '', // 사용자 이름 입력값
            password: '', // 비밀번호 입력값
            error: '', // 에러 메시지
            success: '' // 성공 메시지
        };
    },
    methods: {
        async register() {
            // 초기화: 이전 에러/성공 메시지를 비웁니다.
            this.error = '';
            this.success = '';

            // 입력값 검증: 사용자 이름과 비밀번호가 비어있는지 확인
            if (!this.username || !this.password) {
                this.error = 'Username and password are required.';
                return;
            }

            try {
                // 백엔드 API 호출
                const response = await axios.post('/api/auth/register.php', {
                    username: this.username,
                    password: this.password
                });

                // API 응답 처리
                if (response.data.success) {
                    // 성공 메시지 표시
                    this.success = 'Registration successful! Redirecting to login...';
                    // 2초 후 로그인 페이지로 리다이렉트
                    setTimeout(() => {
                        this.$router.push('/login');
                    }, 2000);
                } else {
                    // 실패 메시지 표시
                    this.error = response.data.message || 'Registration failed.';
                }
            } catch (err) {
                // 에러 처리: 네트워크 또는 기타 문제 발생 시
                this.error = 'An error occurred. Please try again later.';
            }
        }
    }
};
</script>

<style>
/* 에러 메시지 스타일 */
.error {
    color: red;
    font-weight: bold;
}

/* 성공 메시지 스타일 */
.success {
    color: green;
    font-weight: bold;
}

/* 입력 필드 스타일 */
input {
    display: block;
    margin: 10px 0;
    padding: 8px;
    width: 100%;
    max-width: 300px; /* 최대 너비 제한 */
}

/* 버튼 스타일 */
button {
    padding: 10px 20px;
    background-color: #4caf50; /* 기본 배경색 */
    color: white;
    border: none;
    cursor: pointer;
}

/* 버튼 호버 효과 */
button:hover {
    background-color: #45a049; /* 호버 시 배경색 */
}
</style>
