<template>
    <div class="create-post">
        <h1>게시글 작성</h1>
        <form @submit.prevent="submitPost">
            <div>
                <label for="title">제목</label>
                <input type="text" id="title" v-model="title" placeholder="제목을 입력하세요" required />
            </div>
            <div>
                <label for="content">내용</label>
                <textarea
                    type="text"
                    id="content"
                    v-model="content"
                    placeholder="내용을 입력하세요"
                    required
                ></textarea>
            </div>
            <button type="submit" :disabled="loading">
                {{ loading ? '작성중...' : '작성하기' }}
            </button>
        </form>
    </div>
</template>
<script>
import axios from 'axios';

export default {
    name: 'CreatePost',
    data() {
        return {
            title: '',
            content: '',
            loading: false
        };
    },
    methods: {
        // 게시글 작성 요청 메서드
        async submitPost() {
            // 로딩 상태 활성화
            this.loading = true;

            const formData = new FormData();
            formData.append('title', this.title); // 제목 데이터 추가
            formData.append('content', this.content); // 내용 데이터 추가

            try {
                //서버에 POST 요청
                const response = await axios.post('http://tnswn10203.dothome.co.kr/api/posts/create.php', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    },
                    withCredentials: true // 쿠키를 포함한 요청
                });
                // 요청 성공 시 처리
                if (response.status === 200) {
                    alert(response.data.message || '게시글이 성공적으로 작성되었습니다.');
                    this.$router.push('/');
                } else {
                    alert(response.data.message || '게시글 작성에 실패했습니다.');
                }
            } catch (error) {
                console.log('게시글 작성 중 오류 : ', error);
                if (error.response && error.response.data && error.response.data.message) {
                    alert(`오류: ${error.response.data.message}`);
                } else {
                    alert('게시글 작성 중 알 수 없는 오류가 발생했습니다.');
                }
            } finally {
                this.loading = false;
            }
        }
    }
};
</script>
<style scoped>
.create-post {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
}
.create-post h1 {
    text-align: center;
    margin-bottom: 20px;
}
.create-post form div {
    margin-bottom: 15px;
}
.create-post label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
}
.create-post input,
.create-post textarea {
    width: 100%;
    padding: 10px;
    background-color: #ffffff;
    color: #000000;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
}
.create-post button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}
</style>
