<template>
    <div class="edit-post">
        <h1>게시글 수정</h1>
        <form @submit.prevent="updatePost">
            <div>
                <label for="title">제목</label>
                <input type="text" id="title" v-model="title" placeholder="제목을 입력하세요" required />
            </div>
            <div>
                <label for="content">내용</label>
                <textarea
                    type="text"
                    id="content"
                    v-model="content"
                    placeholder="내용을 입력하세요"
                    required
                ></textarea>
            </div>
            <button type="submit" :disabled="loading">
                {{ loading ? '작성중...' : '작성하기' }}
            </button>
        </form>
    </div>
</template>
<script>
import axios from 'axios';
export default {
    name: 'EditPost',
    data() {
        return {
            title: '',
            content: '',
            loading: false
        };
    },
    created() {
        // 컴포넌트가 실행될 때 실행되는 로직
        const postId = this.$route.params.id; // url파라미터로 전달된 게시글 ID 가져오기
        console.log('수정할 게시글 ID:', postId);

        // api 호출로 기존 게시글 데이터 가져와
        axios
            .get(`http://tnswn10203.dothome.co.kr/api/posts/get_post.php?id=${postId}`)
            .then((response) => {
                const post = response.data; // 서버에서 가져온 게시글 데이터
                this.title = post.title; // 제목데이터 초기화
                this.content = post.content; // 내용 데이터 초기화
            })
            .catch((error) => {
                // 데이터 로드 중 오류 처리
                console.error('게시글 데이터를 가져오는 중 오류발생: ', error);
                alert('게시글 데이터를 불러오는데 실패했습니다.');
            });
    },
    methods: {
        // 게시글 수정 요청 메서드
        async updatePost() {
            this.loading = true;

            const formData = new FormData();
            formData.append('id', this.$route.params.id); // 게시글 ID 추가
            formData.append('title', this.title); // 제목 데이터 추가
            formData.append('content', this.content); // 내용 데이터 추가

            try {
                // 게시글 수정 API 호출
                const response = await axios.post('http://tnswn10203.dothome.co.kr/api/posts/update.php', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    },
                    withCredentials: true // 세션 사용시 필요
                });

                console.log('응답:', response.data);

                if (response.status === 200) {
                    alert(response.data.message || '게시글이 성공적으로 수정되었습니다.');
                    this.$router.push('/');
                } else {
                    alert(response.data.message || '게시글 수정에 실패했습니다.');
                }
            } catch (error) {
                console.error('게시글 수정 중 오류: ', error);
                if (error.response && error.response.data && error.response.data.message) {
                    alert(`오류: ${error.response.data.message}`);
                } else {
                    alert('게시글 수정 중 알 수 없는 오류가 발생했습니다.');
                }
            } finally {
                this.loading = false;
            }
        }
    }
};
</script>
<style scoped>
.edit-post {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
}
.edit-post h1 {
    text-align: center;
    margin-bottom: 20px;
}
.edit-post form div {
    margin-bottom: 15px;
}
.edit-post label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
}
.edit-post input,
.edit-post textarea {
    width: 100%;
    padding: 10px;
    background-color: #ffffff;
    color: #000000;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
}
.edit-post button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}
</style>
