<template>
    <div class="post-item">
        <h2>{{ post.title }}</h2>
        <p>{{ post.content }}</p>
        <button @click="editPost" :disabled="loading">수정</button>
        <button @click="deletePost" :disabled="loading">
            {{ loading ? '삭제중...' : '삭제' }}
        </button>
    </div>
</template>

<script>
import axios from 'axios'; //서버와 통신하기 위해 필요함
export default {
    name: 'PostItem',
    props: {
        //부모 컴포넌트로부터 전달받은 게시글 데이터
        post: {
            type: Object, //객체타입으로 지정
            required: true //필수 속성으로 설정
        }
    },
    data() {
        return {
            loading: false
        };
    },
    methods: {
        //게시글 수정 페이지로 이동
        editPost() {
            this.$router.push({ name: 'EditPost', params: { id: this.post.id } });
        },
        //게시글 삭제 메서드
        async deletePost() {
            //삭제 확인 메세지 표시
            if (confirm('정말로 삭제하시겠습니까?')) {
                this.loading = true; //로딩 상태 활성화
                try {
                    const formData = new FormData();
                    formData.append('id', this.post.id); //게시글 id 추가

                    //서버에 삭제 요청
                    //multipart/form-data는 파일 데이터나 여러 키-값 쌍을 포함하는 데이터를 전송할때 사용
                    const response = await axios.post(
                        'http://tnswn10203.dothome.co.kr/api/posts/delete.php', //삭제 api
                        formData,
                        {
                            headers: {
                                'Content-Type': 'multipart/form-data'
                            }
                        }
                    );
                    //삭제 성공 시 처리
                    console.log(response.data.message);
                    alert('게시글이 삭제되었습니다.'); //사용자에게 알림.
                    //부모 컴포넌트에 삭제된 게시글 id를 이벤트로 전달
                    this.$emit('postDeleted', this.post.id);
                } catch (error) {
                    //오류처리
                    if (error.response && error.response.data && error.response.data.message) {
                        console.error('서버 오류:', error.response.data.message); //수정
                        alert(`오류: ${error.response.data.message}`); //수정
                    }
                } finally {
                    this.loading = false; //로딩 상태 해제
                }
            }
        }
    }
};
</script>

<style scoped>
.post-item {
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 16px;
    margin-bottom: 16px;
    background-color: #f9f9f9;
    transition: box-shadow 0.2s ease-in-out;
}
.post-item:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.post-item h2 {
    font-size: 1.5rem;
    color: #555;
    margin-bottom: 16px;
}

.post-item p {
    font-size: 1rem;
    color: #555;
    margin-bottom: 16px;
}

.post-item button {
    margin-right: 8px;
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
}

.post-item button:first-of-type {
    background-color: #007bff;
    color: white;
    transition: background-color 0.2s ease;
}

.post-item button:first-of-type:hover {
    background-color: #0169d9;
}

.post-item button:last-of-type {
    background-color: #fc3d50;
    color: white;
    transition: background-color 0.2s ease;
}

.post-item button:last-of-type:hover {
    background-color: #b02a37;
}
.post-item button:disabled {
    background-color: #ccc;
    cursor: not-allowed; /* 커서 비활성화 */
}
</style>
