<template>
    <div class="home">
        <h1>게시판</h1>
        <router-link to="/create" class="create-link">게시글 작성하기</router-link>
        <div v-if="loading" class="loading">게시글 불러오는 중...</div>
        <div v-else>
            <div v-if="posts.length > 0">
                <div v-for="post in posts" :key="post.id" class="post-item">
                    <PostItem :post="post" @postDeleted="removePost" />
                </div>
            </div>
            <div v-else class="no-posts">게시글이 없습니다.</div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import PostItem from '@/components/PostItem.vue';
export default {
    name: 'Home',
    components: {
        PostItem
    },
    data() {
        return {
            //게시글 목록
            posts: [],
            //로딩 상태
            loading: true
        };
    },
    created() {
        this.fetchPosts();
    },
    methods: {
        async fetchPosts() {
            try {
                const response = await axios.get('http://tnswn10203.dothome.co.kr/api/posts/get_post.php');
                this.posts = response.data;
            } catch (error) {
                console.log('게시글 데이터를 불러오는 중 오류 발생:', error);
                alert('게시글 데이터를 불러오는데 실패했습니다.');
            } finally {
                this.loading = false;
            }
        },
        //삭제된 게시글 제거
        removePost(deletedId) {
            this.posts = this.posts.filter((post) => post.id !== deletedId);
        }
    }
};
</script>

<style scoped>
.home {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
}

h1 {
    text-align: center;
    margin-bottom: 20px;
}
.create-link {
    display: inline-block;
    margin-bottom: 20px;
    padding: 10px 15ps;
    background-color: #007bff;
    color: white;
    text-decoration: none;
    border-radius: 4px;
    font-size: 16px;
    text-align: center;
}
.create-link:hover {
    background-color: #0056b3;
}

.loading {
    text-align: center;
    font-size: 18px;
    color: #555;
}

.no-posts {
    text-align: center;
    font-size: 18px;
    color: #888;
}

.post-item {
    margin-bottom: 20px;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
}
</style>
